/* --- Globale app indstillinger --- */
.App {
    max-width: 1280px;
    margin: 0 auto;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
/* --------------------------------- */

/* ---- Global navigationsmenu ---- */
.globalnavigation {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 15px 0;
    text-align: center;
}

.globalnavigation a {
    width: 100px;
    height: 25px;
    padding-top: 4px;
    text-decoration: none;
    color: black;
}

/* Ændr farver på det aktive menupunkt */
.globalnavigation a.active {
    background-color: #40505f;
    color: white;
}

/* ------------------------------- */


/* ---- Forside-banner (hundebillede) ---- */
.forsidebanner {
  position: relative;
  text-align: center;
}

.forsidebillede {
    max-width: 100%;
}

.centreroverbillede {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.forsidebilledtekst {
    color: #ffffff;
    font-size: 65px;
}

.forsidetekstbaggrund {
    background-color: black;
    opacity: 0.7;
    width: 60%;
    height: 45%;
}

/* --------------------------------------- */

/* ----------- Kvinde og hund ------------ */
.kvindeoghund {
    margin: 50px 0;
    display: flex;
    align-content: flex-start;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
}

.kvindeoghund div {
    width: 600px;
}

.kvindeoghund h1 {
    margin: 0;
}

/* --------------------------------------- */

/* ---------- Info-kort ---------- */

.kortraekke {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.kort {
    position: relative;
    width: 240px;
    margin: 10px;
    box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
}

.adminkort {
    cursor: pointer;
}

.kort img {
    max-width: 100%;
}

.kort .tekst {
    text-align: center;
    padding: 0 10px 50px;
}

.kort .tekst p:nth-of-type(1) {
    min-height: 65px;
}

.kort h3 {
    text-align: center;
    margin-bottom: 5px;
}

.laesmereknap {
    position: absolute;
    background: #805719;
    border-radius: 5px;
    color: white;
    padding: 10px;
    width: 42%;
    bottom: 10px;
    left: 10px;
    border: none;  
}

.bestilknap {
    position: absolute;
    background: #40505f;
    border-radius: 5px;
    color: white;
    padding: 10px;
    width: 42%;
    bottom: 10px;
    right: 10px;
    border: none;  
}



/* ---------------------------- */

/* ------ Kvalitetsbanner ------ */
.kvalitetsbanner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
    height: 120px;
    width: 100%;
    background-color: #40505f;
    color: white;
    margin: 50px 0;
}

.ikonjustering {
    vertical-align: middle;
    margin-right: 5px;
}

.kvalitetstekst {
    font-size: large;
}



/* ------------------------------- */


/* ---------- Footer ---------- */
.sidefod {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-around;
    margin-bottom: 50px;
}

.tidstabel td, tr {
    text-align: left;
}

.tidstabel td {
    padding-right: 10px;
    padding-left: 0;
}

/* ---------------------------- */

/* ------- Fejl i formular -------- */
.tekst-fejl {
    color: red;
}

.tekst-fejl:first-letter {
    text-transform: uppercase;
}

/* ------------------------------- */

/* ---------- form styling ---------- */

form {
    margin: auto;
    box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.25);
    text-align: center;
    padding: 1em 1em 2em;
    background-color: var(--white);
    max-width: 380px;
    animation: fadeIn 0.5s;
}

form button {
    padding: 12px 15px;
    font-size: 1em;
    width: 100%;
    max-width: 350px;
}

button {
    font-weight: 300;
    text-align: center;
    cursor: pointer;
    border: none;
    color: var(--text-color-light);
    background-color: var(--green);
    letter-spacing: 0.05em;
    text-transform: uppercase;
    padding: 0.5em 1em;
    font-size: 0.8em;
    display: block;
    margin: 1em auto 0;
}

button.btn-outline {
    background-color: var(--light-green);
    color: var(--green);
    border: solid 2px var(--green);
}

.btn-sign-out {
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    font-size: 0.7em;
    padding: 2.032em 1em;
}

input {
    margin: 1em auto;
    width: 100%;
    max-width: 350px;
    padding: 1em;
    box-sizing: border-box;
    display: block;
    background: var(--white);
    border: solid 1px gray;
}

.image-preview {
    width: 100%;
    max-width: 350px;
    height: 200px;
    object-fit: cover;
    margin: auto;
    display: block;
    cursor: pointer;
}

form .file-input {
    display: none;
}

/* ---------------------------------- */

/* -------- Bestillingsoversigt tabelrække ---------- */

.bestillingsoversigtstabel {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .bestillingsoversigtstabel td, .bestillingsoversigtstabel th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .bestillingsoversigtstabel tr:nth-child(even){background-color: #f2f2f2;}
  
  .bestillingsoversigtstabel tr:hover {background-color: #ddd;}
  
  .bestillingsoversigtstabel th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
  }

/* -------------------------------------------------- */